import topicInfo from '@/components/topicDetail/topicInfo.vue';
import commonList from '@/components/common/commonList.vue';
import loadMore from "@/components/common/loadmore.vue";
export default {
	components: {
		topicInfo,
		commonList,
		loadMore
	},
	data() {
		return {
			hotList: [
				{
					title: '【新人必读】 uni-app实战第二季商城类项目开发text-ellipsis'
				},
				{
					title: '【新人必读】 uni-app实战第二季商城类项目开发text-ellipsis'
				}
			],
			tabBars: [{ name: '默认' }, { name: '最新' }],
			//默认列表
			list1: [],
			//最新
			list2: [],
			tabindex: 0,
			loadText1:'上拉加载更多',
			loadText2:'上拉加载更多'
		};
	},
	computed: {
		listData() {
			return this.tabindex == 0 ? this.list1 : this.list2;
		},
		loadText(){
			return this.tabindex==0?this.loadText1:this.loadText2
		}
	},
	onLoad(e) {
		if (e.item) {
			let item = JSON.parse(e.item);
			this.item = item;
		}
		this.list1 = demo;
	},
	//触底事件
	onReachBottom() {
		// console.log('上拉加载更多。。。')
		this.loadMore()
	},
	methods: {
		changeTab(index) {
			this.tabindex = index;
		},
		//上拉加载更多
		loadMore(){
			let index=this.tabindex;
			if(this.loadText!=="上拉加载更多") return;
			//设置上拉加载状态处于加载中...
			this['loadText'+(index+1)]='加载中...'
			//请求数据
			setTimeout(()=>{
				this['list'+(index+1)]=[...this['list'+(index+1)],...this['list'+(index+1)]];
				this['loadText'+(index+1)]='上拉加载更多'
			},2000)
		}
	}
};
